@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,400;1,500;1,600;1,700;1,800&display=swap);
.navbar{display:flex;justify-content:space-between;align-items:center;position:fixed;top:0px;z-index:11;padding:0px 50px;width:100%;height:100px;background-color:rgba(10,25,47,0.85);-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px)}@media (max-width: 705px){.navbar{justify-content:center;flex-direction:column}}@media (max-width: 450px){.navbar{padding:0px 20px}}.navbar__links{display:flex;font-size:medium}@media (max-width: 705px){.navbar__links{margin-top:0.7rem}}@media (max-width: 450px){.navbar__links{display:none}}.navbar__links ::marker{color:#64ffda}.navbar__links li{margin:0 18px}.navbar__links li:hover a{color:#64ffda}.navbar__links li a{color:#8892b0}

.hero{display:flex;margin:auto;flex-direction:column;align-items:flex-start;min-height:100vh;padding:0}@media (max-width: 480px) and (min-height: 700px){.hero{padding-bottom:10vh}}.hero h1{margin:0 0 30px 4px;color:#64ffda;font-size:large}@media (max-width: 480px){.hero h1{margin:0 0 20px 2px}}@media (max-width: 450px){.hero h1{font-size:small}}.hero h2{color:#ccd6f6;font-size:5rem}@media (max-width: 600px){.hero h2{font-size:4rem}}@media (max-width: 480px){.hero h2{font-size:3rem}}@media (max-width: 450px){.hero h2{font-size:2rem}}.hero h3{margin-top:10px;color:#8892b0;line-height:0.9;font-size:4rem}@media (max-width: 600px){.hero h3{font-size:3rem}}@media (max-width: 450px){.hero h3{font-size:1.5rem}}.hero p{margin:20px 0 0;max-width:500px;color:#8892b0;font-size:large;font-family:DMMono-Regular;letter-spacing:0.7px}@media (max-width: 450px){.hero p{font-size:medium}}.hero p>a{color:#64ffda;text-decoration:underline}.hero>a{color:#64ffda;background-color:transparent;border:1px solid #64ffda;border-radius:7px;padding:1.25rem 1.75rem;line-height:1;text-decoration:none;cursor:pointer;margin-top:50px}

.wrapper{width:100%;max-width:1600px;min-height:100vh;padding:150px 150px 0 150px}@media (max-width: 883px){.wrapper{padding:150px 60px 0 60px}}@media (max-width: 768px){.wrapper{padding:150px 80px 0 80px}}@media (max-width: 608px){.wrapper{padding:100px 40px 0 40px}}@media (max-width: 450px){.wrapper{padding:100px 20px 0 20px}}

.socialHandlers{width:40px;position:fixed;bottom:0px;left:40px;right:auto;z-index:10;color:#ccd6f6}@media (max-width: 883px){.socialHandlers{display:none}}.socialHandlers .links{display:flex;flex-direction:column;align-items:center;margin:0px;padding:0px;list-style:none}.socialHandlers .links .link{margin:5px auto;padding:5px;font-size:x-large}.socialHandlers .links .link:last-child::after{content:'';display:block;width:1px;height:90px;margin:0px auto;background-color:#ccd6f6}

.emailHandlers{width:40px;position:fixed;bottom:0px;font-family:DMMono-Regular;right:40px;z-index:10;color:#ccd6f6}@media (max-width: 883px){.emailHandlers{display:none}}.emailHandlers .email{display:flex;flex-direction:column;align-items:center;position:relative}.emailHandlers .email>a{margin:20px auto;padding:10px;letter-spacing:0.1em;-webkit-writing-mode:vertical-rl;-ms-writing-mode:tb-rl;writing-mode:vertical-rl;position:relative;display:flex;align-items:center}.emailHandlers .email>a::after{content:'';display:block;width:1px;height:90px;margin:2px auto;background-color:#ccd6f6}

.about{display:flex;margin:auto;flex-direction:column;align-items:flex-start;min-height:100vh;padding:0}.about__header{display:flex;align-items:center;position:relative;margin:10px 0px 40px;width:100%;white-space:nowrap}.about__header::before{position:relative;counter-increment:section 1;content:"0" counter(section) ".";margin-right:10px;color:#64ffda;font-weight:400}.about__header::after{content:'';display:block;position:relative;width:300px;height:1px;margin-left:20px;background-color:#8892b0}.about__metadata{display:grid;grid-template-columns:3fr 2fr;grid-gap:50px;gap:50px;color:#8892b0;line-height:1.5rem;font-size:large;font-family:DMMono-Italic;letter-spacing:1.5px}.about__metadata a{color:#64ffda;text-decoration:underline}.about__metadata p{padding:0 0 10px}.about__metadata .skills-list{display:grid;grid-template-columns:repeat(2, minmax(140px, 200px));padding:0px;margin:20px 0px 0px;overflow:hidden;list-style:none;font-size:smaller;line-height:normal}.about__metadata .skills-list li{position:relative;margin-bottom:10px;padding-left:20px}.about__metadata .skills-list li::before{content:'▹';position:absolute;left:0px;color:#64ffda;font-size:small;line-height:12px}

.project{margin:3rem 0;position:relative;display:grid;grid-gap:10px;gap:10px;grid-template-columns:repeat(12, 1fr);align-items:center}@media (max-width: 560px){.project__context,.project__image{grid-column:1 / -1 !important}}.project:nth-of-type(2n+1) .project__context{grid-column:7 / -1;text-align:right}.project:nth-of-type(2n+1) .project__image{grid-column:1 / 8}@media (max-width: 560px){.project:nth-of-type(2n+1) .project__image{flex-direction:row}}.project:nth-of-type(2n+1) .project__context>.techs{flex-direction:row-reverse}@media (max-width: 560px){.project:nth-of-type(2n+1) .project__context>.techs{flex-direction:row}}.project__context{flex:0.3 1;display:flex;flex-direction:column;justify-content:center;z-index:10;position:relative;grid-area:1 / 1 / -1 / 7}@media (max-width: 560px){.project__context{padding:2rem}}.project__context .header .feature__line{margin-bottom:20px;color:#64ffda;font-size:small}.project__context .header .project__name{margin-top:20px;font-size:clamp(24px, 5vw, 28px)}@media (min-width: 768px){.project__context .header .project__name{margin:0px 0px 20px}}.project__context .description{box-shadow:0 10px 30px -15px #8892b0;transition:200;position:relative;z-index:8;padding:25px;border-radius:5px;background-color:#112240;color:#ccd6f6;font-size:medium;font-family:DMMono-Medium}@media (max-width: 560px){.project__context .description{background-color:transparent;box-shadow:none}}.project__context .techs{display:flex;flex-wrap:wrap;position:relative;z-index:2;margin:25px 0px 10px;padding:0px;list-style:none}.project__context .techs span{margin:0px 20px 5px 0px;color:#8892b0;font-size:small;white-space:nowrap}.project__context .links{display:flex;flex-direction:row-reverse}.project__context .links a{font-size:larger;margin:0 10px;padding:5px;border-radius:9px}.project__context .links a:hover{background-color:rgba(17,34,64,0.5)}.project__image{box-shadow:0 10px 30px -15px #64ffda;transition:200;position:relative;z-index:1;grid-area:1 / 6 / -1 / -1;flex:0.7 1;height:100%}.project__image .img{border-radius:8px;mix-blend-mode:multiply;-webkit-filter:grayscale(50%) contrast(1) brightness(75%);filter:grayscale(50%) contrast(1) brightness(75%);height:100%}@media (max-width: 560px){.project__image .img{-webkit-filter:grayscale(60%) contrast(1) brightness(45%);filter:grayscale(60%) contrast(1) brightness(45%)}}@media (min-width: 560px){.project:hover .project__image .img img{-webkit-filter:none;filter:none}}.project__image img{transition:opacity 500ms linear 0s;object-fit:cover;-webkit-filter:blur(5px);filter:blur(5px);width:100%;height:100%;border:8px}

.works{display:flex;margin:auto;flex-direction:column;align-items:flex-start;min-height:100vh;padding:0}.works__header{display:flex;align-items:center;position:relative;margin:10px 0px 40px;width:100%;white-space:nowrap}.works__header::before{position:relative;counter-increment:section 1;content:"0" counter(section) ".";margin-right:10px;color:#64ffda;font-weight:400}.works__header::after{content:'';display:block;position:relative;width:300px;height:1px;margin-left:20px;background-color:#8892b0}@media (max-width: 350px){.works__header{font-size:larger}}

.app{transition:background-color 1.5s cubic-bezier(0.65, 0.05, 0.36, 1);min-height:100vh}.footer{display:flex;height:100%;margin:3rem auto;justify-content:center;font-family:DMMono-Italic;font-size:smaller}

@font-face{font-family:neuemachina-ultrabold;src:url(/static/media/neuemachina-ultrabold.afcd8980.woff2)}@font-face{font-family:DMMono-Medium;src:url(/static/media/DMMono-Medium.d9b50c90.ttf)}@font-face{font-family:DMMono-Italic;src:url(/static/media/DMMono-Italic.0785017e.ttf)}@font-face{font-family:DMMono-Light;src:url(/static/media/DMMono-Light.cd228c19.ttf)}@font-face{font-family:DMMono-Regular;src:url(/static/media/DMMono-Regular.1f879091.ttf)}*{padding:0;margin:0;box-sizing:border-box;-webkit-user-select:none;-ms-user-select:none;user-select:none}::-webkit-scrollbar{width:5px}::-webkit-scrollbar-track{background-color:#ebebeb;border-radius:10px}::-webkit-scrollbar-thumb{border-radius:10px;background:#6d6d6d}body{margin:0;font-family:neuemachina-ultrabold;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:white;background-color:#0a192f;position:relative;min-height:100vh;width:100%;scroll-behavior:smooth;overflow-x:hidden}@media (max-width: 450px){body{overflow-x:hidden}}a{text-decoration:none;color:#fff}

